@import url('https://fonts.googleapis.com/css2?family=Edu+AU+VIC+WA+NT+Hand:wght@400..700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Crimson+Pro:ital,wght@0,200..900;1,200..900&family=Edu+AU+VIC+WA+NT+Hand:wght@400..700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');

/* Base header styles */
.header {
    display: flex;
    align-items: center;
    color: white;
    background-color: black;
    position: sticky;
    top: 0;
    z-index: 2000;
    padding: 10px 20px;
}

.header__left {
    width: 40%;
}

.header__left h1 {
    text-align: center;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    letter-spacing: 1px;
    padding: 20px 0;
    font-size: 1.5rem; /* Responsive font size */
}

.header__left > h1 > span {
    color: yellow;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.header__right {
    display: flex;
    width: 60%;
    justify-content: space-evenly;
    align-items: center;
}

.header__right > a > div {
    font-size: 1rem;
    cursor: pointer;
    transition: transform 200ms ease-in-out;
}

.header__right > a > div:hover {
    transform: scale(1.1);
    background-color: #f64c08;
    color: white;
    padding: 5px 20px;
    border-radius: 2px;
    letter-spacing: 1px;
    font-family: "Ubuntu";
}

/* Responsive Design */

/* For large devices (desktops, tablets in landscape) */
@media (max-width: 1200px) {
    .header__left h1 {
        font-size: 1.4rem;
    }

    .header__right > a > div {
        font-size: 0.95rem;
    }
}

/* For medium devices (tablets, smaller desktops) */
@media (max-width: 992px) {
    .header__left h1 {
        font-size: 1.3rem;
    }

    .header__right > a > div {
        font-size: 0.9rem;
        padding: 4px 15px;
    }
}

/* For small devices (landscape phones, tablets) */
@media (max-width: 768px) {
    .header {
        flex-direction: column;
        text-align: center;
    }

    .header__left {
        width: 100%;
    }

    .header__right {
        width: 100%;
        justify-content: center;
        margin-top: 10px;
    }

    .header__right > a > div {
        font-size: 0.85rem;
        padding: 4px 10px;
        margin: 5px 0;
    }
}

/* For extra small devices (portrait phones) */
@media (max-width: 576px) {
    .header__left h1 {
        font-size: 1.2rem;
    }

    .header__right > a > div {
        font-size: 0.8rem;
        padding: 3px 8px;
        margin: 3px 0;
    }
}
