@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Base styles */
.topcontent {
    color: white;
    text-align: center;
    padding: 20px;
}

.topcontent__container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    max-width: 90%;
}

.topcontent__container > p {
    letter-spacing: 1px;
    padding-left: 50px;
    color: black;
}

.topcontent__container > a > button {
    margin-top: 20px;
    border: none;
    border-radius: 3px;
    outline: none;
}

.topcontent__container > h1 {
    font-size: 2rem; /* Responsive font size */
    font-weight: bolder;
    font-family: "Montserrat", sans-serif;
}

/* Button styles */
.topcontent__container .download {
    color: white;
    background-color: #f64c08;
    font-size: 1rem;
    padding: 10px 30px;
    font-family: "Montserrat", sans-serif;
    font-weight: 300;
    cursor: pointer;
    margin-right: 20px;
}

.topcontent__container .work {
    color: white;
    border: solid 1px white;
    background-color: transparent;
    font-size: 1rem;
    padding: 9px 45px;
    font-family: "Montserrat", sans-serif;
    font-weight: 300;
    cursor: pointer;
}

.topcontent__container .download:hover {
    color: #f64c08;
    background-color: white;
    font-weight: 600;
}

.topcontent__container .work:hover {
    color: black;
    background-color: white;
    font-weight: 600;
}

/* Responsive design */
@media (max-width: 1200px) {
    .topcontent__container > h1 {
        font-size: 2rem;
    }

    .topcontent__container .download,
    .topcontent__container .work {
        font-size: 0.9rem;
        padding: 8px 25px;
    }
}

@media (max-width: 992px) {
    .topcontent__container {
        max-width: 80%;
    }

    .topcontent__container > h1 {
        font-size: 1.8rem;
    }

    .topcontent__container .download,
    .topcontent__container .work {
        font-size: 0.85rem;
        padding: 8px 20px;
    }
}

@media (max-width: 768px) {
    .topcontent__container {
        top: 40%;
        left: 50%;
        transform: translate(-50%, -40%);
    }

    .topcontent__container > h1 {
        font-size: 1.6rem;
    }

    .topcontent__container .download,
    .topcontent__container .work {
        font-size: 0.8rem;
        padding: 7px 15px;
        margin-right: 10px;
    }
}

@media (max-width: 576px) {
    .topcontent__container {
        top: 30%;
        left: 50%;
        transform: translate(-50%, -30%);
        max-width: 95%;
    }

    .topcontent__container > h1 {
        font-size: 1.4rem;
    }

    .topcontent__container .download,
    .topcontent__container .work {
        font-size: 0.75rem;
        padding: 6px 10px;
        margin-right: 5px;
    }
}
