.about {
   background: url(../assets/portfolio\ image.jpg) center/cover no-repeat;
   height: 100vh;
}

/* Responsive Design */
@media (max-width: 1200px) {
   .about {
       height: 90vh; /* Slightly reduce height on large screens */
   }
}

@media (max-width: 992px) {
   .about {
       height: 80vh; /* Reduce height on medium screens */
   }
}

@media (max-width: 768px) {
   .about {
       height: 70vh; /* Further reduce height on small screens */
   }
}

@media (max-width: 576px) {
   .about {
       height: 60vh; /* Minimal height for extra small screens */
       background-position: top center; /* Adjust position if needed */
   }
}
