.skillcontainer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 100px 20px; /* Reduced margin for better responsiveness */
    flex-wrap: wrap; /* Allows wrapping on smaller screens */
}

.skillcontainer__text {
    color: white;
    flex: 0.6;
    text-align: left;
    max-width: 500px;
    margin-bottom: 20px;
}

.skillcontainer__text h2 {
    font-size: 2rem; /* Responsive font size */
}

.skillcontainer__image {
    flex: 0.4;
    text-align: center;
    margin-right: 50px; /* Reduced margin for smaller screens */
}

.skillcontainer .skillcontainer__image img {
    width: 50vw; /* Use vw for responsiveness */
    max-width: 300px; /* Maximum width to avoid oversized images */
    margin-top: 50px; /* Adjusted margin */
    border-radius: 3px;
    object-fit: contain;
}

.skillcontainer > h5 {
    font-family: "Montserrat", sans-serif;
}

.skillcontainer__slider > .MuiLinearProgress-colorPrimary {
    background-color: rgb(88, 88, 88);
}

.skillcontainer-slider1 > span > .MuiLinearProgress-barColorPrimary {
    background-color: tomato;
}
.skillcontainer-slider2 > span > .MuiLinearProgress-barColorPrimary {
    background-color: red;
}
.skillcontainer-slider3 > span > .MuiLinearProgress-barColorPrimary {
    background-color: green;
}
.skillcontainer-slider4 > span > .MuiLinearProgress-barColorPrimary {
    background-color: yellow;
}
.skillcontainer-slider5 > span > .MuiLinearProgress-barColorPrimary {
    background-color: violet;
}
.skillcontainer-slider6 > span > .MuiLinearProgress-barColorPrimary {
    background-color: grey;
}
.skillcontainer-slider7 > span > .MuiLinearProgress-barColorPrimary {
    background-color: pink;
}
.skillcontainer-slider8 > span > .MuiLinearProgress-barColorPrimary {
    background-color: mediumspringgreen;
}
.skillcontainer-slider9 > span > .MuiLinearProgress-barColorPrimary {
    background-color: goldenrod;
}
/* Additional slider color styles here */

@media (max-width: 1200px) {
    .skillcontainer {
        margin: 80px 10px;
    }

    .skillcontainer__text h2 {
        font-size: 1.8rem;
    }

    .skillcontainer .skillcontainer__image img {
        width: 40vw;
        max-width: 250px;
    }
}

@media (max-width: 992px) {
    .skillcontainer__text h2 {
        font-size: 1.6rem;
    }

    .skillcontainer .skillcontainer__image img {
        width: 45vw;
        max-width: 220px;
    }

    .skillcontainer__image {
        margin-right: 30px;
    }
}

@media (max-width: 768px) {
    .skillcontainer {
        flex-direction: column-reverse;
        margin: 60px 5px;
        text-align: center; /* Center text on smaller screens */
    }

    .skillcontainer__text {
        text-align: center; /* Center text */
        max-width: 100%;
    }

    .skillcontainer__text h2 {
        font-size: 1.4rem;
    }

    .skillcontainer__image {
        margin-right: 0;
        margin-bottom: 20px;
    }

    .skillcontainer .skillcontainer__image img {
        width: 60vw;
        max-width: 200px;
        margin-top: 30px;
    }
}

@media (max-width: 576px) {
    .skillcontainer__text h2 {
        font-size: 1.2rem;
    }

    .skillcontainer .skillcontainer__image img {
        width: 70vw;
        max-width: 180px;
        margin-top: 20px;
    }
}
